}

.hero-section__container {
  position: relative;
  z-index: 90;
  max-width: 961px;
  margin: 0 auto;
}

.hero-section__container h1 {
  margin: 0;
  margin-bottom: 16px;
  font-weight: 200;
  font-size: 6.375rem;
  font-family: "RoundedMp", sans-serif;
  line-height: 1.14;
}

.hero-section__container h1 span {
  font-weight: 800;
}

.hero-section__container h1 span span:nth-of-type(1) {
  display: block;
  padding-left: 67px;
}

.hero-section__container h1 span span:nth-of-type(2) {
  display: block;
