}

.hero-section__container p {
  margin: 0;
  margin-bottom: 48px;
  padding-left: 435px;
  font-size: 1.25rem;
  line-height: 1.4;
}

.catalog {
  position: relative;
  z-index: 10;
  padding-bottom: 565px;
}

.catalog .layer {
  position: absolute;
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
}

.catalog .layer._bg {
  bottom: 0;
  left: 50%;
  z-index: -1;
  width: 100%;
  height: 1108px;
  transform: translateX(-50%);
}

.catalog .layer._ball1 {
  bottom: 250px;
  left: 130px;
  z-index: 15;
  width: 408px;
  height: 1117px;
}

.catalog .layer._ball2 {
  bottom: 170px;
  left: 27px;
  width: 251px;
  height: 992px;
}

.catalog__link {
  align-self: flex-start;
}

.catalog__link span {
  padding: 33px 82px;
}

.catalog__title {
  max-width: 723px;
  margin-bottom: 58px;
}

.catalog__title h2 {
  margin: 0;
  font-weight: 200;
  font-size: 5rem;
  line-height: 1.2;
}

.catalog__title h2 span {
  font-weight: 800;
}

.catalog__container {
  display: flex;
  justify-content: space-between;
}

.catalog__col:nth-child(1) {
  width: 379px;
}

.catalog__col:nth-child(2) {
  display: flex;
  flex-direction: column;
  width: calc(100% - 432px);
}

.catalog__list {
  width: 286px;
  padding: 30px 0 30px 12px;
  background: linear-gradient(#bea1e8, #d5bff4);
  border-radius: 20px;
  box-shadow: inset 0 1px 0 #8e64cc;
}

.catalog__item {
  position: relative;
  z-index: 10;
  padding: 9px 15px 13px 28px;
  overflow: hidden;
  color: #573e7b;
  font-size: 1.125rem;
  border-radius: 22px 0 0 22px;
  cursor: pointer;
}

.catalog__item._active::before {
  transform: translateX(0);
}

.catalog__item::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 22px 0 0 22px;
  transform: translateX(100%);
  transform-origin: right;
  transition: transform 0.2s ease-in;
  content: "";
}

.catalog__item:hover::before {
  transform: translateX(0);
}

.catalog__products {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 40px;
}

.catalog__product {
  grid-column: 4 span;
  height: 502px;
  background-color: #fff;
  border-radius: 20px;
}

.catalog__product ._wrap {
  position: relative;
  height: 100%;
  padding: 31px 26px;
  overflow: hidden;
}

.catalog__product img {
  position: absolute;
  top: 0;
  left: 50%;
  display: block;
  width: 100%;
  margin: 0 auto;
  object-fit: cover;
  transform: translateX(-50%);
  transition: transform 0.2s ease-in;
}

.catalog__product-info {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  transition: transform 0.2s ease-in;
}

.catalog__product-price {
  color: #345f7e;
  font-size: 1.25rem;
}

.catalog__product-quant {
  color: #345f7e;
  font-size: 1rem;
}

.catalog__product-legend {
  display: block;
  margin-top: auto;
  margin-bottom: 16px;
  color: #345f7e;
  font-weight: 700;
  font-size: 1.125rem;
}

.catalog__product-button {
  max-width: 343px;
}

@media (min-width: 1025px) {
  .catalog__product-button {
    position: absolute;
    bottom: 31px;
    left: 50%;
    max-width: 343px;
    transform: translateY(100%) translateX(-50%);
    visibility: hidden;
    opacity: 0;
    transition: transform 0.2s ease-in, opacity 0.2s ease-in, visibility 0.2s ease-in;
  }
  .catalog__product:hover .catalog__product-button {
    transform: translateY(0) translateX(-50%);
    visibility: visible;
    opacity: 1;
  }
  .catalog__product:hover .catalog__product-info {
    transform: translateY(-100px);
  }
  .catalog__product:hover img {
    transform: translateX(-50%) scale(0.8);
  }
}

.catalog__product-wrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.catalog__show-more {
  position: relative;
  display: inline-block;
  margin: 0 auto;
  margin-top: 40px;
  margin-bottom: 100px;
  color: #573e7b;
  font-weight: 700;
  font-size: 1.25rem;
  text-align: center;
}

.catalog__show-more::before {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #573e7b;
  transition: opacity 0.2s ease-in;
  content: "";
}

@media (min-width: 1025px) {
  .catalog__show-more:hover::before {
    opacity: 0;
  }
}

.catalog__text {
  max-width: 675px;
  margin: 0;
  margin-bottom: 48px;
  font-weight: 800;
  font-size: 3rem;
  font-family: "RoundedMp", sans-serif;
  line-height: 1.23;
}

.cart-button {
  display: flex;
  margin-top: 20px;
}

.cart-button__text {
  width: calc(100% - 143px);
  padding: 27px 50px;
  color: #fff;
  text-transform: uppercase;
  background-color: #00a888;
  border-radius: 15px 0 0 15px;
}

.cart-button__quant {
  display: flex;
  align-items: center;
  width: 143px;
  padding: 23px 25px;
  background-color: #098e6a;
  border-radius: 0 15px 15px 0;
}

.cart-button__quant .icon-cart {
  display: block;
  width: 23px;
  height: 23px;
  margin-right: 16px;
  color: #098e6a;
  fill: #fff;
}

.cart-button__input {
  width: 32px;
  color: #fff;
  text-align: center;
  background-color: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}

.cart-button__plus {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 15px;
  height: 15px;
  font-weight: 700;
  font-size: 23px;
  cursor: pointer;
}

.cart-button__minus {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 15px;
  height: 15px;
  font-weight: 700;
  font-size: 23px;
  cursor: pointer;
}

.discount {
  position: relative;
  margin-top: -4px;
  padding-top: 90px;
  padding-bottom: 176px;
  background-color: #abc3ff;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.discount::before {
