  left: 0;
  z-index: 15;
  width: 100%;
  height: 781px;
}

.discount__title {
  max-width: 818px;
  margin: 0 auto;
  margin-bottom: 64px;
}

.discount__title h2 {
  margin: 0;
  font-weight: 200;
  font-size: 5rem;
  line-height: 1.2;
}

.discount__title h2 span {
  font-weight: 800;
}

